.Home {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.4)), to(rgba(0, 0, 0, 0.4))),
    url("https://images.unsplash.com/photo-1533777324565-a040eb52facd?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=530b619fd121a8c84d86329f48fbe611&auto=format&fit=crop&w=1321&q=80");
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url("https://images.unsplash.com/photo-1533777324565-a040eb52facd?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=530b619fd121a8c84d86329f48fbe611&auto=format&fit=crop&w=1321&q=80");
  background: -o-linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url("https://images.unsplash.com/photo-1533777324565-a040eb52facd?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=530b619fd121a8c84d86329f48fbe611&auto=format&fit=crop&w=1321&q=80");
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url("https://images.unsplash.com/photo-1533777324565-a040eb52facd?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=530b619fd121a8c84d86329f48fbe611&auto=format&fit=crop&w=1321&q=80");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  min-height: 100vh;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: rgba(255, 255, 255, 0.829);
}

.Home h1 {
  font-size: 5em;
}

.Home h2,
.Home h1 {
  margin: 0;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
}

.Home h4 {
  font-weight: 400;
  font-size: 2em;
  max-width: 85vw;
  text-align: center;
}

.Home_Buttons {
  width: 90vw;
}

.Home_Buttons ul {
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  -ms-flex-pack: center;
      justify-content: center;
}

.Home_Buttons ul li {
  margin: 0 35px;
  border: 1px solid red;
  /* padding: 1em 2em; */
  min-width: 20%;
  max-width: 20%;
  text-align: center;
  font-size: 1.2em;
}

.Home_Buttons ul li a {
  display: block;
  width: 100%;
  height: 100%;
  line-height: 72px;
}



