.NavBar {
  display: -ms-flexbox;
  display: flex;
  min-width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  padding-top: 2.5vh;
  background: rgb(44, 34, 34);
  font-weight: 700;
  z-index: 10;
}

.NavBar a {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
}

.NavBar h4 {
  min-width: 50%;
  text-align: center;
  margin: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 2em;
}

.Navigation {
  min-width: 50%;
  display: -ms-flexbox;
  display: flex;
}
.Navigation ul {
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  -ms-flex-pack: center;
      justify-content: center;
}

.Navigation ul li {
  margin: 0 2em;
  text-transform: uppercase;
  opacity: 0.8;
  font-weight: 400;
}

.Navigation ul li:hover {
  opacity: 1;
}

.MenuBtn {
  position: absolute;
  z-index: 3;
  right: 35px;
  top: 35px;
  cursor: pointer;
  opacity: 0;
}
.btn {
  width: 28px;
  height: 3px;
  margin: 0 0 5px 0;
  background: #fff;
  opacity: 0;
}

.Display-Menu {
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg);
  position: relative;
  /* transition-delay: 0.1s; */
  z-index: 10;
  /* transform: ; */
}

.Line1 {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
  background: #e95a4f;
  margin-right: 2px;
}

.Line2 {
  background: #f4cf3a;
}
.Line3 {
  background: #71ac4a;
}

.Menu-Slide {
  -webkit-transform: translateX(100vw);
      -ms-transform: translateX(100vw);
          transform: translateX(100vw);
  -webkit-transition-duration: 1s;
       -o-transition-duration: 1s;
          transition-duration: 1s;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  opacity: 0.95;
}

.Menu-Slideout {
  -webkit-transform: translateX(-100vw);
      -ms-transform: translateX(-100vw);
          transform: translateX(-100vw);
}

@media only screen and (max-width: 992px) {
  .NavBar h4 {
    font-size: 26px;
    min-width: 40%;
  }
  .Navigation {
    min-width: 60%;
    font-size: 15px;
  }
}

@media only screen and (max-width: 768px) {
  .NavBar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -webkit-transform: translateX(-100vw);
        -ms-transform: translateX(-100vw);
            transform: translateX(-100vw);
  }
  .NavBar h4 {
    font-size: 26px;
    min-width: 100%;
  }
  .Navigation {
    min-width: 100%;
    font-size: 15px;
  }
  .Navigation ul {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100%;
    padding-left: 0;
  }
  .Navigation ul li {
    text-align: center;
    margin: 3vh 0;
  }

  .NavBtn {
    position: absolute;
    top: 35px;
    right: 35px;
    opacity: 0.8;
    padding: 0.3em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    /* background: #e42424;
    border: 1px solid #e42424;
    border-radius: 5px; */
  }

  .NavBtn:hover {
    cursor: pointer;
    opacity: 1;
  }
  .btn {
    opacity: 0.8;
  }
}



