.Sub2 {
  height: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.4)), to(rgba(0, 0, 0, 0.4))),
    url("https://images.pexels.com/photos/256318/pexels-photo-256318.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260")
      no-repeat;
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url("https://images.pexels.com/photos/256318/pexels-photo-256318.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260")
      no-repeat;
  background: -o-linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url("https://images.pexels.com/photos/256318/pexels-photo-256318.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260")
      no-repeat;
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url("https://images.pexels.com/photos/256318/pexels-photo-256318.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260")
      no-repeat;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  width: 99vw;
}

.Section2 {
  width: 99vw;
  height: 70vh;
}
.Box2 {
  width: 35vw;
  margin: 0 auto;
  height: 100%;
  -webkit-transform: translate(12vw, -12vh);
      -ms-transform: translate(12vw, -12vh);
          transform: translate(12vw, -12vh);
  background: #f4cf3a;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  /* box-shadow: 1px 1px 8px #000; */
  color: #fff;
  position: relative;
  z-index: 1;
}

.Box2 p,
.Box3 p {
  max-width: 90%;
  line-height: 44px;
  margin: 0 auto;
}

.Text2 {
  background: #3b444d;
}

#read {
  border: 1px solid #fff;
}

#read:hover {
  border: 1px solid #e42424;
}

@media only screen and (max-width: 1200px) {
  .Box2 {
    width: 45vw;
    -webkit-transform: translate(19vw, -9vh);
        -ms-transform: translate(19vw, -9vh);
            transform: translate(19vw, -9vh);
  }
}

@media only screen and (max-width: 992px) {
  .Box2 {
    width: 55vw;
    -webkit-transform: translate(19vw, -9vh);
        -ms-transform: translate(19vw, -9vh);
            transform: translate(19vw, -9vh);
  }
}

@media only screen and (max-width: 768px) {
  .Box2 {
    width: 100vw;
    -webkit-transform: none;
        -ms-transform: none;
            transform: none;
  }
}



